@import "mixins";

body {
	margin: 0 auto;
	font-family: 'Roboto', sans-serif; }
html, body {
	width: 100%;
	height: 100%; }

header {
	position: relative;
	z-index: 10;
	@include box-shadow(0 0 30px rgba(0,0,0,0.5));
	.navbar {
		margin-bottom: 0;
		.container-fluid {
			max-width: 1366px;
			margin: auto; } }
	.navbar-default {
		background: #fff;
		border: none; }
	.navbar-brand {
		padding-top: 13px;
		padding-bottom: 13px;
		height: auto; }
	.navbar-nav > li > a {
		padding-top: 28px;
		padding-bottom: 28px; }
	.navbar-default .navbar-nav > li > a:hover {
		color: $main-text-hover; }
	.navbar-default .navbar-nav > li > a {
		@include transition-ease(0.2s);
		color: #000; }
	.navbar-form {
		padding: 0;
		margin: 0;
		a {
			padding-top: 25px;
			padding-bottom: 25px; } } }
.btn {
	border-radius: 0;
	border: none;
	text-transform: uppercase;
	font-size: 18px;
	@include transition-ease(0.2s); }

.green_circle {
	position: absolute;
	width: 288px;
	height: 288px;
	border-radius: 50%;
	background: $main-green; }
.jumbotron {
	// max-width: $mw
	// margin: auto
	background: url('/img/jumb_bg.png') no-repeat 50% 50%;
	background-size: cover;
	position: relative;
	padding: 1px;
	margin: 0;
	height: 100%z;
	max-height: 774px;
	border: none;
	overflow: hidden;

	@include block;

	.jumbotron_window_img {
		position: absolute;
		top: 172px;
		right: -30px; }
	.green_circle {
		top: 25px;
		right: 650px; }
	.jumbotron_link {
		margin-top: 460px;
		width: 388px;
		display: block;
		img {
			width: 90%; } }
	.jumbotron_h {
		margin-bottom: 50px; }
	.jumbotron_button:hover {
		background: $main-blue-hover; }
	.jumbotron_button {
		background: $main-blue;
		padding: 18px; } }


