.guarant:after {
	content: "";
	position: absolute;
	width: 100%;
	bottom: 0px;
	left: 0;
	height: 15px;
	background: transparent;
	background: -moz-linear-gradient(bottom, rgba(78,160,255,0.1) 0%, rgba(0,0,0,0) 100%) /* FF3.6-15 */;
	background: -webkit-linear-gradient(bottom, rgba(78,160,255,0.1) 0%,rgba(0,0,0,0) 100%) /* Chrome10-25,Safari5.1-6 */;
	background: linear-gradient(to top, rgba(78,160,255,0.1) 0%,rgba(0,0,0,0) 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */; }
.guarant {
	position: relative;
	@include block;
	padding-bottom: 100px;
	background: url('/img/guarant_bg.png') no-repeat 0% 0%;
	// background-size: cover
	// @include box-shadow(inset 0 0px 30px 0px rgba(0,0,0,0.5))
	margin-bottom: 100px;
	.green_circle {
		left: -80px;
		top: -130px; }
	.guarant_first_left {
		img {
			min-height: 570px; } }
	.guarant_first_right {
		margin-top: 250px; }

	.guarant_h {
		font-size: 45px;
		margin-bottom: 35px; }
	.guarant_text {
		font-size: 14px;
		margin-bottom: 15px; }
	.azure {
		width: 170px;
		margin-top: 10px; }

	.guarant_second {
		margin-top: 200px; }
	.guarant_second_right {
		img {
			position: relative;
			z-index: 20; } }
	.guarant_second_left {
		margin-top: 95px; }

	.guarant_second_left_ul {
		margin-bottom: 40px;
		list-style-type: none;
		list-style-image: url('/img/dot.png');
		padding: 0; }
	.guarant_second_left_li:first-child {
		max-width: 420px; }
	.guarant_second_left_li {
		padding-left: 15px;
		margin-left: -15px;
		margin-bottom: 20px; }
	.guarant_second_left_button:hover {
		background: $main-blue-hover; }
	.guarant_second_left_button {
		background: $main-blue;
		padding: 18px 60px; } }

.test {
	@include block;
	.test_left {
		.test_h {
			font-size: 45px;
			margin-bottom: 35px; }
		// .test_calc_text_block
		// 	position: relative
		// .test_calc_text_block:after
		// 	content: ''
		// 	width: 3px
		// 	height: 100%
		// 	position: absolute
		// 	top: 0
		// 	right: 50px
		// 	background: #eaeaea

		.test_calc {
			max-width: 565px;
			border: 5px solid #eaeaea;
			padding: 50px;
			.test_calc_block {
				display: inline-block; }
			.test_calc_text {
				width: 70%;
				padding-left: 8%; }
			.test_calc_price {
				width: 20%;
				padding-left: 5%;
				border-left: 3px solid #EAEAEA;
				div {
					margin-bottom: 10px; }
				.price_number {
					display: inline-block;
					min-width: 30px;
					text-align: center; } }
			.test_calc_slider {
				margin-top: 50px;
				.test_calc_slider_value {
					margin-top: 15px;
					font-size: 0;
					letter-spacing: -1px;
					text-align: center;
					.calc_slider_value_number {
						font-size: 14px;
						letter-spacing: normal;
						display: inline-block;
						width: 16%; } }
				.slider-horizontal {
					width: 100%;
					.slider-track {
						height: 5px; } }
				.slider {
					.tooltip.top {
						display: none; } }
				.slider-handle {
					background: $main-green;
					border: 6px solid #D0EFC6;
					width: 31px;
					height: 31px;
					top: -8px; }
				.slider-track {
					border-radius: 0; }
				.slider-selection {
					border-radius: 0;
					background: $main-green;
					@include box-shadow(none); } } }

		.test_calc_expl {
			margin-top: 30px;
			padding-left: 7px;
			max-width: 565px; } }

	.test_right {
		padding-top: 140px;
		.test_right_text {
			font-size: 14px;
			margin-bottom: 15px; }
		.test_right_button:hover {
			background: $main-blue-hover; }
		.test_right_button {
			background: $main-blue;
			padding: 18px 55px;
			margin-top: 45px; } } }

