.faq:after {
	content: '';
	position: absolute;
	top: 50%;
	left: 55%;
	width: 3px;
	height: 80%;
	background: #999;
	@include translate(-50%, -50%); }
.faq {
	@include block;
	position: relative;
	background: #E3F5DD;
	.faq_h {
		font-size: 24px;
		margin-bottom: 30px; }
	.faq_text {
		max-width: 370px;
		margin-bottom: 25px; }
	.navbar-form {
		padding: 0;
		margin-bottom: 30px;
		.form-control:focus {
			border: 1px solid #999999;
			@include box-shadow(0 0 30px 5px rgba(0, 0, 0, .075)); }
		.form-group {
			position: relative; }
		.form-group:after {
			content: url('/img/search-icon.png');
			position: absolute;
			top: 5px;
			right: 7px; }
		.form-control {
			padding-right: 30px;
			min-width: 390px; } }

	.faq_left_button:hover {
		background: $main-green-hover; }
	.faq_left_button:last-child {
		margin-right: 0; }
	.faq_left_button {
		background: $main-green;
		text-align: center;
		min-width: 180px;
		padding-top: 18px;
		padding-bottom: 18px;
		margin-right: 55px; }
	.faq_right_button:hover {
		background: $main-blue-hover; }
	.faq_right_button {
		background: $main-blue;
		padding-top: 18px;
		padding-bottom: 18px; }

	.faq_right {
		padding-left: 10%; } }


