footer {
	color: #fff;
	@include block;
	padding-bottom: 10px;
	background: #333;
	.footer_ul {
		list-style-type: none;
		padding: 0;
		// margin: 0
		margin-bottom: 25px;
		.footer_li:last-child {
			margin-right: 0; }
		.footer_li {
			margin-right: 25px;
			display: inline-block;
			a:hover {
				text-decoration: none;
				color: $main-text-hover; }
			a {
				color: #fff;
				@include transition-ease(0.2s);
				font-size: 18px; } } } }

